import { useState } from "react";
import { signupData } from "./signupData";

export default function Signup() {
  const [on, setOn] = useState([false], [false], [false], [false]);
  const [check, setCheck] = useState([false], [false], [false], [false]);

  function setCheckArray(e) {
    let newCheck = [...check];
    newCheck[e - 1] = !newCheck[e - 1];
    setCheck(newCheck);
  }

  function setOnArray(e) {
    let newOn = [...on];
    newOn[e - 1] = !newOn[e - 1];
    setOn(newOn);
  }

  // const [state, setState] = useState("");

  function handler(e) {
    if (e === 1) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 2) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 3) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 4) {
      setOnArray(e);
      setCheckArray(e);
    }
  }

  return (
    <div className="signup">
      <div className="signup_title">
        <h2></h2>
      </div>
      <div className="signup_wrap">
        <div className="signup_wrap_title">
          <img src="../../image/login/signup_title.png" alt="signup_title" />
          <p>   .</p>
        </div>
        <form className="signup_clause">
          {signupData.map((v, i) => {
            // 
            if (i <= 2) {
              return (
                <div className="item" key={v.id}>
                  <div className="item_title">
                    <span>
                      {v.title}
                      <strong>()</strong>
                    </span>
                    <label
                      htmlFor={`clause_check${i + 1}`}
                      className={
                        on[i] === true && check[i] === true ? "on" : null
                      }
                      onClick={() => {
                        handler(i + 1);
                      }}
                    >
                      <div className="check_path"></div>
                    </label>
                    <input
                      type="checkbox"
                      id={`clause_check${i + 1}`}
                      checked={
                        on[i] === true && check[i] === true ? true : false
                      }
                      required
                    />
                  </div>
                  <textarea
                    name={`clause${i + 1}`}
                    id={`clause${i + 1}`}
                    disabled
                  >
                    {v.content}
                  </textarea>
                </div>
              );
            }
            // 
            if (i >= 3) {
              return (
                <div className="item" key={v.id}>
                  <div className="item_title">
                    <span>
                      {v.title}
                      <strong>()</strong>
                    </span>
                    <label
                      htmlFor={`clause_check${i + 1}`}
                      className={
                        on[i] === true && check[i] === true ? "on" : null
                      }
                      onClick={() => {
                        handler(i + 1);
                      }}
                    >
                      <div className="check_path"></div>
                    </label>
                    <input
                      type="checkbox"
                      id={`clause_check${i + 1}`}
                      checked={
                        on[i] === true && check[i] === true ? true : false
                      }
                    />
                  </div>
                  <div
                    className="textarea"
                    name={`clause${i + 1}`}
                    id={`clause${i + 1}`}
                    disabled
                  >
                    <table>
                      <tbody>
                        <tr>
                          <th scope="row"></th>
                          <td>
                              (     )
                          </td>
                        </tr>
                        <tr>
                          <th scope="row"></th>
                          <td> , </td>
                        </tr>
                        <tr>
                          <th scope="row">   </th>
                          <td>  </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <p>
                         ,   
                         .
                  </p>
                </div>
              );
            }
          })}

          {/*    */}
          <div className="caution">
            <div className="caution_title">
              <h2> </h2>
            </div>
            <ul className="coution_content">
              <li>    ,  .</li>
              <li>
                        
                      .
              </li>
              <li>
                    Internet Explorer 9.0 ,
                Chrome, Firefox, Safari   .
              </li>
            </ul>
          </div>

          <div className="certify">
            <div className="certify_title">
              <h2> </h2>
            </div>
            <div className="certify_wrap">
              <div className="phone common_certify">
                <img src="../../image/login/signup_phone.png" alt="phone" />
                <h3></h3>
                <p>    </p>
                <input type="submit" value="" className="btn" />
              </div>

              <div className="ipin common_certify">
                <img src="../../image/login/signup_ipin.png" alt="ipin" />
                <h3></h3>
                <p> ID </p>
                <input type="submit" value="" className="btn" />
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
